.detailPage {
    width: 100%;
    padding: 0;
    margin: 0;
    font-family: 'Pretendard', 'Noto Sans KR', sans-serif;
    animation: fadeInUp 0.4s ease forwards;
    background-color: #f8f8f8;
    min-height: 100vh;
    box-sizing: border-box;
}

.detailContainer {
    max-width: 720px;
    margin: 0 auto;
    background-color: #fff;
    padding: 2rem;
    border-radius: 12px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.06);
    box-sizing: border-box;
}

.detailHeader {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 1.5rem;
}

.detailHeader button {
    background: none;
    border: none;
    font-size: 1.2rem;
    color: #00753C;
    cursor: pointer;
    padding: 0;
    font-weight: bold;
}

.detailHeader button:hover {
    color: #004225;
}

.detailTitle {
    font-size: 2rem;
    font-weight: 700;
    color: #222;
    border-bottom: 2px solid #00753C;
    padding-bottom: 0.4rem;
    margin: 1rem 0;
}

.detailImage {
    width: 100%;
    max-height: 360px;
    object-fit: cover;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 1.5rem;
}

.detailInfo {
    font-size: 1.1rem;
    color: #444;
    margin-bottom: 0.8rem;
}

.detailReview {
    font-size: 1rem;
    color: #666;
    line-height: 1.6;
    margin-top: 1rem;
}

/* Animation */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(12px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Responsive */
@media (max-width: 768px) {
    .detailContainer {
        padding: 1.5rem;
    }

    .detailTitle {
        font-size: 1.6rem;
    }

    .detailInfo,
    .detailReview {
        font-size: 0.95rem;
    }
}
